.tableday_title{
    h1{
        font-size: 20px;
    }
}
.my_classes_table{
    margin-bottom: 40px;
    tbody{
        tr{
            th{
                padding: 0 30px;
            }
            td{
                padding: 10px 20px;
                text-align: left;
                h4{
                    display: inline-block;
                    width: 60%;
                    color: #333333;
                }
                &:last-child{
                    vertical-align: middle;
                    text-align: center;
                }
                &:nth-child(3){
                    em{
                        display: inline-block;
                        width: 100%;
                        text-align: center;
                    }
                }
            }
            &:last-child{
                th{
                    border-bottom: 1px solid #eaeaea;
                }
            }
        }
    }
}